'use strict';
import util from '../util';

module.exports = class entity {

    //property

    //constructor
    constructor(data) {
        if (!data) return;
        for (const field of this.fields()) {
            if (util.isExistProperty(data, field)) this[field] = data[field];
        }
    }

    //key of entity
    keys() {
        return [];
    }

    //field
    fields() {
        return [];
    }

    //entity name
    getEntityName() {
        return '';
    }

    


};
